import React, { Fragment, useEffect, useState } from "react";
import { cartAction } from "../Store/cart-Slice";
import { useDispatch } from "react-redux";
import ToTopButton from "../Components/ToTopButton";

import Snackbar from '@mui/material/Snackbar';
import IconButton from '@mui/material/IconButton';
import { useTranslation } from 'react-i18next';
import { database } from "../Firebase"; // 檔案路徑應正確對應 Firebase.js
import { ref, onValue } from "firebase/database";

const Woman = (props) => {
  const dispatch = useDispatch();
  const [listItem, setListItem] = useState([]); // 只存「原始資料」陣列
  const [snackBar, setSnackBar] = useState(false);
  const { t } = useTranslation("common");

  useEffect(() => {
    const dbRef = ref(database, "clothes"); // 對應到資料庫的路徑

    // 使用 Firebase SDK 的 onValue 監聽數據
    const unsubscribe = onValue(dbRef, (snapshot) => {
      const data = snapshot.val();
      if (data) {
        // 過濾符合條件的資料
        const filteredData = Object.values(data).filter(
          (x) => x.type === props.children
        );
        setListItem(filteredData);
      } else {
        setListItem([]);
      }
    });

    // 捲動到頂部
    window.scrollTo({ top: 0, left: 0 });

    // 清理監聽器
    return () => unsubscribe();
  }, [props.children]);

  // 開啟 Snackbar
  const handleOpenSnackBar = () => {
    setSnackBar(true);
  };

  // 關閉 Snackbar
  const handleCloseSnackBar = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    setSnackBar(false);
  };

  // Snackbar 的 action（通常用來放關閉按鈕之類）
  const action = (
    <Fragment>
      <IconButton
        size="small"
        aria-label="close"
        color="inherit"
        onClick={handleCloseSnackBar}
      >
      </IconButton>
    </Fragment>
  );

  return (
    <div className="woman">
      <div className="woman_product">
        {/* 在 render 階段用 map() 產生 JSX，而不是先組好 JSX 再存進 state */}
        {listItem.map((x) => (
          <div key={x.id} className="woman_product_item">
            <img src={x.imgurl} alt="" />
            <div className="woman_product_item_details">
              <span className="woman_product_item_details_title">{x.name}</span>
              <span className="woman_product_item_details_price">£{x.price}</span>
            </div>
            <div className="woman_product_item_cart">
              <span
                onClick={() => {
                  dispatch(
                    cartAction.addItem({
                      id: x.id,
                      name: x.name,
                      img: x.imgurl,
                      price: x.price,
                      qty: 1
                    })
                  );
                  handleOpenSnackBar();
                }}
              >
                <i className="fa-solid fa-cart-arrow-down" />
                {t("add_to_cart")}
              </span>
            </div>
          </div>
        ))}
      </div>

      <ToTopButton />

      <Snackbar
        open={snackBar}
        autoHideDuration={3000}
        onClose={handleCloseSnackBar}
        message={t("added_to_cart")}
        action={action}
      />
    </div>
  );
};

export default Woman;
